import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HTTP } from '@ionic-native/http/ngx';
import { SERVER_URL } from 'src/environments/environment.prod';

@Injectable({
  providedIn: 'root'
})
export class TouristService {

  constructor(private http:HttpClient,private http1:HTTP) { }
  public GetTouristNews(){
    return this.http1.get(SERVER_URL+'News/Index/1/1',{},{})
  }
  public GetFirstCities(){
    return this.http1.get(SERVER_URL+'City/Index/1',{},{})
  }
  public GetCityDetails(id){
    return this.http1.get(SERVER_URL+'City/Details/'+ id,{},{})
  }
  public GetHotels(index){
    return this.http1.get(SERVER_URL+'Directory/Index/2/'+ index,{},{})
  }
  public GetAirborts(index){
    return this.http1.get(SERVER_URL+'Directory/Index/1024/'+ index,{},{})
  }
  public GetRestaurants(index){
    return this.http1.get(SERVER_URL+'Directory/Index/1025/'+ index,{},{})
  }
  public GetTourismCompany(index){
    return this.http1.get(SERVER_URL+'Directory/Index/1023/'+ index,{},{})
  }
  public GetDivingCenter(index){
    return this.http1.get(SERVER_URL+'Directory/Index/5/'+ index,{},{})
  }
}
