import { TouristService } from '../../../Services/tourist.service';
import { Router } from '@angular/router';
import { Component, OnInit,ViewChild } from '@angular/core';
import { Platform, IonSlides } from '@ionic/angular';
import { Images } from 'src/environments/environment.prod';
import { RegisterService } from 'src/app/Services/register.service';
@Component({
  selector: 'app-home',
  templateUrl: './home.page.html',
  styleUrls: ['./home.page.scss'],
})
export class HomePage implements OnInit {
  @ViewChild(IonSlides, {static: true}) slider: IonSlides;

  @ViewChild('slideWithNav2', { static: false }) slideWithNav2: IonSlides;

  sliderTwo: any;
  slideOptsTwo = {
    initialSlide: 0,
    slidesPerView: 3,
    loop: true,
    centeredSlides: false,
    spaceBetween: 5
  };
  slideOpts = {
    effect: 'flip',
    autoplay: {
    delay: 4000
    }
  };

  albumDisplay:boolean = false;
  sliderData:any[]=[]
  photoPath:any = Images
  cities:any[]=[]
  constructor(private router:Router,
    private touristService:TouristService,
    public registerService:RegisterService,
    ) { }
  ionViewWillEnter(){
    this.slider.startAutoplay();
    this.touristService.GetTouristNews().then(res=>{
      this.sliderData = JSON.parse(res.data)
    })
    this.touristService.GetFirstCities().then(res=>{
      this.cities = JSON.parse(res.data)
    })
  }
  ngOnInit() {
  }

  getAlbumList(){
    this.albumDisplay = true
  }

  public ionViewWillLeave(){  
    this.slider.stopAutoplay();
   }
   slidesDidLoad(slides) {
    this.slider.startAutoplay();
  }
  goToCity(id){
    this.router.navigate(['tabs/city-details',id])
  }
  goToLearnList(id){
    this.router.navigate(['./tabs/learn-about',id])
  }
}
